<template>
  <div class="zb_oto">
    <van-sticky>
      <header>
        <div class="back" @click="$router.go(-1)">
          <van-icon name="arrow-left" />
        </div>
        <h3>一对一辅导</h3>
        <div class="right" @click="$router.push('/search')">
          <van-icon name="search" />
        </div>
      </header>
    </van-sticky>
    <!-- 下拉菜单 -->
    <div class="of-head">
      <div
        :class="flag ? 'active' : ''"
        @click="flag ? (flag = !flag) : (flag = !flag) && (flags = false)"
      >
        <font>选择上课时间</font><i></i>
      </div>
      <div
        :class="flags ? 'active' : ''"
        @click="flags ? (flags = !flags) : (flags = !flags) && (flag = false)"
      >
        <font>选择老师条件</font><i></i>
      </div>
    </div>
    <!-- 选择上课时间 -->
    <div class="time" v-show="flag">
      <div class="date">
        <p>日期</p>
        <div class="weekday">
          <span>周一</span>
          <span>周二</span>
          <span>周三</span>
          <span>周四</span>
          <span>周五</span>
          <span>周六</span>
          <span>周日</span>
        </div>
        <div class="time-day">
          <p><font></font><span></span></p>
        </div>
      </div>
      <div class="rinle">
        <p>时段</p>
        <div class="ipt">
          <p @click="showss = true">
            <input placeholder="开始时间" disabled />
            <van-icon name="clock-o" />
          </p>
          <font>-</font>
          <p @click="showss = true">
            <input placeholder="结束时间" disabled /><van-icon name="clock-o" />
          </p>
        </div>
      </div>
    </div>
    <van-popup v-model="showss" position="bottom" :style="{ height: '42%' }">
      <van-datetime-picker
        v-model="currentTime"
        type="time"
        :min-hour="0"
        :max-hour="23"
      />
    </van-popup>

    <!-- 选择老师条件 -->
    <div class="teacher-module" v-show="flags">
      <div class="teacher_type tm-kind">
        <p>老师类型</p>
        <div>
          <span
            :class="index == item.level_id ? 'active' : ''"
            @click="act(item.level_id)"
            v-for="item in obj.otoTeacherLevel"
            :key="item.level_id"
            >{{ item.level_name }}</span
          >
        </div>
      </div>
      <div class="tm-kind">
        <p>只看</p>
        <div>
          <van-checkbox
            v-model="is_collect"
            @click="
              is_collect ? (arr.is_collect = '1') : (arr.is_collect = '0')
            "
            name="a"
            shape="square"
            size=".3rem"
          >
            已关注
            <template #icon="props">
              <img
                class="img-icon"
                :src="
                  props.checked
                    ? 'data:image/png;base64,'
                    : 'data:image/png;base64,'
                "
              />
            </template>
          </van-checkbox>
          <van-checkbox
            v-model="is_attended"
            @click="
              is_attended ? (arr.is_attended = '1') : (arr.is_attended = '0')
            "
            name="b"
            shape="square"
            size=".3rem"
            >上过课的
            <template #icon="props">
              <img
                class="img-icon"
                :src="
                  props.checked
                    ? 'data:image/png;base64,'
                    : 'data:image/png;base64,'
                "
              />
            </template>
          </van-checkbox>
        </div>
      </div>
      <!-- 性别 -->
      <div class="tm-kind">
        <p>性别</p>
        <div><span @click="sex">男</span><span @click="sex">女</span></div>
      </div>
      <!-- 年级 -->
      <div class="teacher_type tm-kind">
        <p>{{ obj.attrList[0].attr_name }}</p>
        <div>
          <span
            :class="active == item.attr_val_id ? 'active' : ''"
            @click="acti(item.attr_val_id)"
            v-for="item in obj.attrList[0].child"
            :key="item.attr_val_id"
            >{{ item.attr_val_name }}</span
          >
        </div>
      </div>
      <!-- 学课 -->
      <div class="teacher_type tm-kind">
        <p>{{ obj.attrList[1].attr_name }}</p>
        <div>
          <span
            :class="ati == i.attr_val_id ? 'active' : ''"
            @click="activ(i.attr_val_id)"
            v-for="i in obj.attrList[1].child"
            :key="i.attr_val_id"
            >{{ i.attr_val_name }}</span
          >
        </div>
      </div>
    </div>
    <div class="oto-btn" v-show="flag || flags">
      <button @click="chongzhi">重置</button
      ><button @click="getList">确定</button>
    </div>
    <!-- 列表展示 -->
    <div class="a" v-show="!flag && !flags">
      <div
        class="zb_otItem"
        @click="token?$router.push('/oto-plan?id=' + item.teacher_id):show=true"
        v-for="item in list"
        :key="item.teacher_id"
      >
        <van-image round width=".8rem" height=".8rem" :src="item.avatar" />
        <div>
          <p>
            <span>{{ item.real_name }} </span>
            <span class="sp"> {{ item.is_collect == 0 ? "" : "已关注" }}</span>
          </p>
          <p>{{ item.sex == 0 ? "男" : "女" }} {{ item.teach_age }}年教龄</p>
        </div>
        <div class="yuyue">预约</div>
      </div>
    </div>


    <!-- 遮罩层 -->
    <app-liji :show="show" @s="s"></app-liji>
  </div>
</template>

<script>
import appLiji from "../components/Liji";
import { Toast } from "vant";
import { getCourse, getOtoCourseOptions } from "../util/api";

export default {
  // 组件名称
  name: "demo",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {appLiji},
  // 组件状态值
  data() {
    return {
      show:false,
      token:localStorage.getItem('token')||'',
      // 日期
      currentTime:'',
      showss: false,
      list: [],
      obj: {},
      result: [],
      flag: false,
      flags: false,
      index: "",
      active: "",
      ati: "",
      arr: {
        page: 1,
        limit: 10,
        start_time: "",
        end_time: "",
        level_id: "",
        is_collect: "",
        is_attended: "",
        sex: "",
        attr_val_id: "",
      },
      is_collect: false,
      is_attended: false,
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  methods: {
    s(val) {
      this.show = val;
    },
    // 重置
    chongzhi() {
      (this.arr = {
        page: 1,
        limit: 10,
        start_time: "",
        end_time: "",
        level_id: "",
        is_collect: "",
        is_attended: "",
        sex: "",
        attr_val_id: "",
      }),
        (this.index = ""),
        (this.active = ""),
        (this.ati = ""),
        (this.is_collect = false),
        (this.is_attended = false),
        this.getList();
    },
    // 类型
    act(index) {
      this.index = index;
      this.arr.level_id = index;
      console.log(this.arr);
    },
    // 年级
    acti(index) {
      this.active = index;
      this.arr.attr_val_id = `${index},${this.ati}`;
    },
    // 学课
    activ(index) {
      this.ati = index;
      this.arr.attr_val_id = `${this.active},${index}`;
    },
    // 性别
    sex() {
      [...event.target.parentNode.childNodes].forEach((item) => {
        item.classList.remove("active");
      });
      event.target.classList.add("active");

      [...event.target.parentNode.childNodes].forEach((item) => {
        if (item.classList.contains("active")) {
          if (item.html == "男") {
            this.arr.sex = "0";
          } else {
            this.arr.sex = "1";
          }
        }
      });
    },
    onClickLeft() {
      Toast("返回");
    },
    onClickRight() {
      Toast("按钮");
    },

    async getList() {
      this.flag = false;
      this.flags = false;
      let { data } = await getCourse(this.arr);
      this.list = data.data;
      // console.log(this.list);
      let { data: res } = await getOtoCourseOptions();
      this.obj = res.data;
      // console.log(this.obj);
    },
  },
  created() {},
  mounted() {
    this.getList();
  },
};
</script> 

<style lang="scss" scoped>
// 日期
.time {
  margin: 2.66667vw;
  > div {
    background-color: #fff;
    border-radius: 0.1rem;
    margin-bottom: 0.3rem;
    > p {
      font-size: 3.2vw;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: #595959;
      padding: 4vw 0 2.66667vw 2.66667vw;
    }
    .weekday {
      display: flex;
      justify-content: space-around;
      align-items: center;
      height: 0.4rem;
      span {
        font-size: 2.66667vw;
        font-family: PingFangSC-Regular;
        font-weight: 400;
        color: #595959;
      }
    }
    .ipt {
      display: flex;
      margin-left: 3.46667vw;
      justify-content: space-around;
      align-items: center;
      p {
        width: 38vw;
        height: 8vw;
        background: #fff;
        border-radius: 1.06667vw;
        border: 0.26667vw solid #bfbfbf;
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-sizing: border-box;
        padding: 0 0.2rem;
        input {
          border: none;
          width: 26.66667vw;
          height: 6.66667vw;
          line-height: 6.66667vw;
          font-size: 3.46667vw;
          padding-left: 2.66667vw;
        }
      }
      font {
        width: 9.33333vw;
        height: 8.53333vw;
        line-height: 8.53333vw;
        text-align: center;
        color: #b7b7b7;
        font-size: 3.73333vw;
      }
    }
  }
  .rinle {
    height: 25.06667vw;
  }
}
// 下拉菜单
.of-head {
  display: flex;
  height: 11.2vw;
  background: #fff;
  div {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font {
      font-size: 4vw;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: #8c8c8c;
    }
    i {
      width: 2.4vw;
      height: 1.33333vw;
      background-size: 100% 100%;
      margin-left: 1.33333vw;
      background: url("data:image/png;base64,")
        no-repeat;
      background-size: 100% 100%;
    }
  }
  .active {
    font {
      color: #eb6100;
    }
    i {
      background: url("data:image/png;base64,")
        no-repeat;
      background-size: 100% 100%;
    }
  }
}
.img-icon {
  height: 0.3rem;
}
.zb_oto {
  height: 100%;
  background-color: #f0f2f5;
  header {
    height: 0.88rem;
    line-height: 11.73333vw;
    background-color: #fff;
    text-align: center;
    font-size: 0.3rem;
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 10px;
    align-items: center;
    h3 {
      font-weight: 400;
      color: #595959;
    }
    .van-icon {
      font-size: 0.3rem;
    }
  }
  //   列表样式
  .zb_otItem {
    width: 95%;
    height: 1.62rem;
    margin: 0.1rem auto;
    background: #fff;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 0.1rem;
    div {
      width: 4rem;
      p {
        color: #b7b7b7;
        font-size: 3.2vw;
        span {
          font-size: 0.33rem;
          color: #595959;
        }
        .sp {
          font-size: 2.93333vw;
          color: #ea7a2f;
        }
      }
    }
    .yuyue {
      width: 1.4rem;
      height: 0.62rem;
      line-height: 0.62rem;
      background: #ebeefe;
      border-radius: 0.6rem;
      font-size: 3.73333vw;
      font-weight: 400;
      color: #eb6100;
      border: none;
      text-align: center;
    }
  }
  //   下拉菜单
  .van-dropdown-menu {
    font-size: 0.3rem !important;
    .teacher {
      min-height: 12rem;
      background: #fff;
      border-top: 1px solid #f1f1f1;
    }
  }
}
.teacher-module {
  padding: 0 4vw 10.66667vw;
  background: #fff;
  border-top: 1px solid #f1f1f1;
  .teacher_type {
    div {
      flex-wrap: wrap;
    }
  }
}
.tm-kind {
  padding-bottom: 2.66667vw;
  border-bottom: 1px solid #f1f1f1;
  p {
    padding: 4.26667vw 0 2.66667vw;
    font-size: 3.2vw;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: #595959;
  }
  div {
    display: flex;
    span {
      margin: 0 2vw 1.86667vw;
      width: 18.7vw;
      height: 8.8vw;
      background: #f5f5f5;
      border-radius: 0.8vw;
      font-size: 3.46667vw;
      font-family: PingFangSC-Regular;
      font-weight: 400;
      color: #646464;
      text-align: center;
      line-height: 8.8vw;
    }
    .active {
      color: #eb6100;
      background: #ebeefe;
    }
  }
}
.oto-btn {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: 11.73333vw;
  button {
    width: 50%;
    display: inline-block;
    line-height: 11.73333vw;
    height: 11.73333vw;
    font-size: 4.53333vw;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    text-align: center;
    color: #eb6100;
    background: #fff;
    border: none;
    z-index: 99;
  }
  button:nth-child(2) {
    background: #eb6100;
    color: #fff;
  }
}
</style>
